import React from "react";
import { connect } from "react-redux";






/* ################################################### */
/*          UI Framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import Paper                                        from 'material-ui/Paper';
import {
    Card , CardHeader , CardText
}                                                   from 'material-ui';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionHome , { ReduxEnumHome }          from '../../_redux/action/reduxAction.home';







/* ################################################### */
/*          helper         */
/* ################################################### */

import HelperStorage		                        from '../_helper/h.storage';
import HelperVerifyAuthorize                        from '../_helper/h.verifyAuthorize';






/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../_abstract/c.abstract';

import ComponentCustomAlert                         from '../_component/alert/cc.alert';
import ComponentCustomLoading                       from '../_component/loading/cc.loading';









/* ################################################### */
/* 		 style		 */
/* ################################################### */

import '../_style/s.app.css';
import './s.home.css';








export default connect( state => {

        let thisState = state[ "ReduxHome" ];
        let stateObj = {};

        if ( thisState ) {
            for ( let i = 0; i < thisState.length; i ++ ) {
                let recordItem = thisState[ i ];

                if ( !recordItem[ "completed" ] ) {

                    if ( ReduxEnumHome.INIT_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    } else if ( ReduxEnumHome.ERROR == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestError';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    }



                    recordItem[ "completed" ] = true;
                }
            }
        }

        return stateObj;

    } ) (

        class ComponentHome extends ComponentAbstract {




            constructor( props ) {
                super( props );


                this.state = {
                    isLoading : false

                    , isError : false
                    , errorMsg : ''


                    , result : []
                }

            }





            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				event
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            _onReady() {
                this.requestInit();
            }






            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				request
        		################################################################################################################################################
        		################################################################################################################################################
        	*/

            requestInit() {
                console.log( 'ComponentHome : requestInit : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {

                                            this.dispatch(
                                                ReduxActionHome.rInit(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , { }
                                                )
                                            )

                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;

                    }
                );

            }
            requestInitDone( result ) {
                console.log( 'ComponentHome : requestInitDone : *** : result => ' , result );

                let resultObj = this.state.result;
                resultObj = result;
                this.setState( {
                    isLoading : false

                    , result : resultObj
                } );

                this.dispatch( ReduxActionHome.rRequestEnd( ) );

            }

            requestError( result ) {
                console.log( 'ComponentHome : requestError : *** : result => ' , result );

                this.setState(
                    { isLoading : false }
                    , () => {

                        this.handlerException(
                            result
                            , () => {
                                this.setState( {
                                    isError         : true
                                    , errorMsg      : result.msg
                                } );
                            }
                        );

                    }
                )

                this.dispatch( ReduxActionHome.rRequestEnd( ) );

            }















            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				render
        		################################################################################################################################################
        		################################################################################################################################################
        	*/

            render() {
                return (

                    <div className={ ' home-padding-10 ' }>

                        <Paper>

                            <Table
                                    selectable={ false }
                                    fixedHeader={ true }
                                    style={ { width: "100%" } } tableLayout="auto">

                                <TableHeader
                                    adjustForCheckbox={ false }
                                    displaySelectAll={ false }>

                                    <TableRow>

                                        <TableHeaderColumn> Task Type </TableHeaderColumn>
                                        <TableHeaderColumn> Pending Task </TableHeaderColumn>

                                    </TableRow>

                                </TableHeader>

                                <TableBody displayRowCheckbox={ false }>
                                    {
                                        this.state.result.map(
                                            ( item , index ) => {

                                                return (
                                                    <TableRow
                                                                key={ '_home_' + index }>

                                                        <TableRowColumn> { index } </TableRowColumn>
                                                        <TableRowColumn> { '10000' } </TableRowColumn>

                                                    </ TableRow>
                                                )
                                            }
                                        )
                                    }
                                </TableBody>

                            </Table>

                        </Paper>


                        <ComponentCustomLoading
                                        visible={ this.state.isLoading }
                        />


                        <ComponentCustomAlert
                            visible={ this.state.isError }

                            title={ 'Error' }
                            msg={ this.state.errorMsg }

                            onClose={ () => {
                                this.setState( {  isError : false } );
                            } }

                            alertType={ 'error' }
                        />

                    </div>

                )
            }

        }

    )



