import React                				from 'react';
import PropTypes                            from 'prop-types';


import {
	Grid , Row , Col
	, Alert
	, Button
} 											from 'react-bootstrap';
import {
	CircularProgress
	, RaisedButton
} 											from 'material-ui';
import Modal                                from 'react-responsive-modal';
import FontAwesome                     		from 'react-fontawesome';








import ComponentAbstract        			from '../../_abstract/c.abstract';







export default class ComponentCustomAlert extends ComponentAbstract {


    static propTypes = {
        visible 		: PropTypes.bool

		, alertType 	: PropTypes.string

		, title 		: PropTypes.string
		, msg 			: PropTypes.string


        , onClose 		: PropTypes.func
    }


    static defaultProps = {
        onClose : () => {}

		, visible : false

		, alertType : ''

		, title : ''
		, msg : ''
    }









	constructor( props ) {
        super( props );
    }










	/*
		################################################################################################################################################
		################################################################################################################################################
				util
		################################################################################################################################################
		################################################################################################################################################
	*/
    backgroundColor() {

    	if ( this.props.alertType == 'error' ) {
    		return '#EF9A9A';

    	} else if ( this.props.alertType == 'warning' ) {
    		return '#FFF59D';

    	} else {
    		return '#90CAF9';

    	}

    }
    fontColor() {

    	if ( this.props.alertType == 'error' ) {
    		return '#B71C1C';

    	} else if ( this.props.alertType == 'warning' ) {
    		return '#F57F17';

    	} else {
    		return '#0D47A1';

    	}

    }
    buttonColor() {

    	if ( this.props.alertType == 'error' ) {
    		return '#C62828';

    	} else if ( this.props.alertType == 'warning' ) {
    		return '#F9A825';

    	} else {
    		return '#1565C0';

    	}

    }









	/*
		################################################################################################################################################
		################################################################################################################################################
				event
		################################################################################################################################################
		################################################################################################################################################
	*/
    onClose() {
        this.props.onClose( this.props.alertType );
    }












	/*
		################################################################################################################################################
		################################################################################################################################################
				render
		################################################################################################################################################
		################################################################################################################################################
	*/
    render() {
    	return (

    		<div>

		        <Modal
						open={ this.props.visible }
						showCloseIcon={ false }
	                    styles={ {
	                        overlay : {
	                            backgroundColor : 'rgba(0, 0, 0, 0.45)'
	                            , zIndex : '1050'
	                        }
	                        , modal : {
	                            background : this.backgroundColor()
	                            , width : '750px'
	                            , height : '200px'
	                            , overflow : 'hidden'
								, position : 'relative'
	                        }
	                    } }

						onEscKeyDown={ () => { } }
	                    onOverlayClick={ () => { } }
						onClose={ () => {
							this.props.onClose();
						} } >

					<div
							style={ {
								color : this.fontColor()
								, border : '0px'
							} } >
						<h3><b> { this.props.title } </b></h3>
					</div>

					<div
							style={ {
								color : this.fontColor()
							} } >

						<p>{ this.props.msg }</p>

					</div>

					<div
							style={ {
								border : '0px'
								, position : 'absolute'
								, bottom : '15px'
								, right : '15px'
							} } >

		            	<RaisedButton       icon={ <FontAwesome name="info-circle" style={{ color : '#FAFAFA' }} /> }
                                            labelPosition="after"
                                            label="OK"
                                            onClick={ () => this.onClose() }
                                            buttonStyle={ { background : this.buttonColor() , borderColor : this.buttonColor() } }
                                            style={ { background : this.buttonColor() } }
                                            labelColor='#FAFAFA'
						/>

					</div>

		        </Modal>

    		</div>

    	);
    }

}



